import React, { Component } from "react";
import MetaHead from "../../components/MetaHead";
import WebHead from "../../components/WebHead";
import Article from "../../components/Article";
import { Layout, TopSection, SearchBar, SearchText } from "./index-css";
import { data } from "../data.json";
import CodeSection from "../../components/CodeSection";

class Home extends Component {
  state = {
    searchResult: false,
    searchResultArrary: [],
    keywords: ""
  };
  searching = e => {
    const keywords = e.target.value.toLowerCase();
    this.setState({
      keywords: e.target.value
    });
    if (keywords.length > 2) {
      // 關鍵字需要三個字以上才開始搜尋
      this.setState({
        searchResult: true
      });
      const findKeywords = data.map(article =>
        article.keywords.findIndex(key => key.includes(keywords))
      );
      var i;
      const newArr = [];
      for (i = 0; i < findKeywords.length; i++) {
        if (findKeywords[i] === -1) {
        } else {
          newArr.push(data[i]);
        }
        this.setState({
          searchResultArrary: newArr
        });
      }
    } else {
      this.setState({
        searchResult: false
      });
    }
  };
  render() {
    return (
      <div style={{ background: "#f2f7fa", minHeight: "105vh" }}>
        <MetaHead title="工作元件區" />
        <WebHead />

        <TopSection>
          <SearchBar placeholder="Quick Search" onChange={this.searching} />
          <CodeSection>{`import React, { Component } from "react";
import { Dialog, Button } from "dawn-react-ui-lib";

class Example extends Component{
    state={
      open: false
    }
    // 使用者自行定義的Button的點擊事件
    openDialog = () => {
      this.setState({
        open:true
      })
    }
    // 傳下去給Dialog關閉畫面的事件
    closeDialog = () => {
      this.setState({
        open:false
      })
    }
    render(){
        return(
          <div>
            <Button
              text="用戶確認對話框"
              onClick={this.openDialog}
              width="250px"
              padding="16px"
            />
            <Dialog
              open={this.state.open}
              handleClose={this.closeDialog}
              contentHeader="標題文字"
              contentBody={<div>內容</div>}
              userAgree
              userAgreeFunc={() => {
                alert("自定義確認事件");
              }}
            />
          </div>
        )
    }
}

export default Example;`}</CodeSection>
        </TopSection>
        {!this.state.searchResult ? (
          <Layout>
            {data.map((articleData, i) => (
              <Article key={i} articleData={articleData} />
            ))}
          </Layout>
        ) : (
          <div>
            <SearchText>
              以下為
              <span>{this.state.keywords}</span>的搜尋結果：
              <span>{this.state.searchResultArrary.length}</span>筆
            </SearchText>
            <Layout>
              {this.state.searchResultArrary.map((articleData, i) => (
                <Article key={i} articleData={articleData} />
              ))}
            </Layout>
          </div>
        )}
      </div>
    );
  }
}

export default Home;
